import * as React from 'react';
import classNames from 'classnames';
import { Container } from '../grid/Grid';
import { H1, BodyBolderText } from '../texts';
import Link from '../link/Link';
import Routes from '../../routing/Routes';
import styles from './PageTitle.module.css';

type Theme = 'dark' | 'brown';

type Props = {
  title: string;
  theme?: Theme;
} & React.HTMLAttributes<HTMLElement>;

const PageTitle: React.FunctionComponent<Props> = ({ className, theme = 'dark', ...props }) => {
  return (
    <nav className={className} {...props}>
      <Container className={styles.container}>
        <Link
          to={Routes.Main()}
          className={classNames(
            {
              [styles.backButtonDark]: theme === 'dark',
              [styles.backButtonBrown]: theme === 'brown',
            },
            styles.backButton
          )}>
          <BodyBolderText
            className={classNames({
              [styles.textDark]: theme === 'dark',
              [styles.textBrown]: theme === 'brown',
            })}>
            Hjem
          </BodyBolderText>
        </Link>
        <H1
          className={classNames(
            {
              [styles.textDark]: theme === 'dark',
              [styles.textBrown]: theme === 'brown',
            },
            styles.title
          )}>
          {props.title}
        </H1>
      </Container>
    </nav>
  );
};

export default PageTitle;
