import * as React from 'react';
import classNames from 'classnames';
import PageTitle from '../../../common/components/pageTitle/PageTitle';
import Section from '../../../common/components/section/Section';
import LoadingIndicator from '../../../common/components/loadingIndicator/LoadingIndicator';
import Layout from '../../../common/components/layout/Layout';
import styles from './GiftCard.module.css';

type Props = {} & React.HTMLAttributes<HTMLElement>;

class GiftCard extends React.Component<Props> {
  componentDidMount() {
    const script = document.createElement('script');
    script.src = 'https://cdn.giftup.app/dist/gift-up.js';
    script.async = true;
    document.body.appendChild(script);
  }

  render() {
    const { className, ...props } = this.props;
    return (
      <Layout className={classNames(styles.container, className)}>
        <Section>
          <PageTitle title="Gavekort" />
          <div className={styles.giftupContainer}>
            {/* Render the giftup frame */}
            <div
              className="gift-up-target"
              data-site-id="1790257e-8db3-45c0-af2d-8e55a7582dd7"
              data-platform="Other">
              <LoadingIndicator />
            </div>
          </div>
        </Section>
      </Layout>
    );
  }
}

export default GiftCard;
