import * as React from 'react';
import BackgroundImage from 'gatsby-background-image';
import { getFluidImageData } from './gatsbyImage.utils';

type Props = {
  asset: any;
  tag?: string;
} & React.HTMLAttributes<HTMLElement>;

const GatsbyBackgroundImage: React.FunctionComponent<Props> = ({
  className,
  tag = 'section',
  ...props
}) => {
  return (
    <BackgroundImage Tag={tag} className={className} fluid={getFluidImageData(props.asset)}>
      {props.children}
    </BackgroundImage>
  );
};

export default GatsbyBackgroundImage;
