import * as React from 'react';
import classNames from 'classnames';
import GatsbyBackgroundImage from '../gatsbyImage/GatsbyBackgroundImage';
import styles from './Section.module.css';

type Props = {
  backgroundImageAsset?: any;
} & React.HTMLAttributes<HTMLElement>;
const Section: React.FunctionComponent<Props> = ({ className, backgroundImageAsset, ...props }) => {
  if (backgroundImageAsset != null) {
    return (
      <GatsbyBackgroundImage
        className={classNames(styles.container, className)}
        asset={backgroundImageAsset}
        tag="section"
        {...props}>
        {props.children}
      </GatsbyBackgroundImage>
    );
  }
  return <section className={classNames(styles.container, className)}>{props.children}</section>;
};

export default Section;
